import styled from '@emotion/styled'
import { GatsbyImage } from 'gatsby-plugin-image'

import {GatsbyImageDataType} from '@/types/gatsby.type'

interface GatsbyImgProps {
    image: GatsbyImageDataType
    alt: string
    className?: string
}

export const PostHeadWrapper = styled.div`
    position: relative;
    width: 100%;
    height: 400px;
    margin-bottom: 64px;

    @media (max-width: 768px) {
        height: 300px;
    }
`

// gatsby-plugin-image   GatsbyImage      ,
//   !important          styled(GatsbyImage)       props    GatsbyImage   
//  !important               .
export const BackgroundImage = styled((props:GatsbyImgProps)=> (
    <GatsbyImage {...props} style={{position: 'absolute'}}/>
))`
    z-index: -1;
    width: 100%;
    height: 400px;
    object-fit:cover;
    filter: brightness(0.25);

    @media (max-width: 768px) {
        height: 300px;
    }
`